#celestial-map { width: 100%; height: auto; margin: 0; padding: 0; display: inline-block; position: relative; }
#celestial-map canvas { display: inline-block; top:0; left: 0; position:absolute; z-index:0; cursor:hand; cursor:grab; cursor:-webkit-grab; }
